  /**
   * Get orderbook for a market
   * Uses HTTP endpoint /clob/v1/orderbook/{marketId} (matches Go client GetOrderbook)
   */
  async getOrderbook(
    marketId: string,
    convertToDecimal: boolean = true,
    depth: number = 20,
  ): Promise<RawOrderbook> {
    let path = `/clob/v1/orderbook/${marketId}`;
    let depthValue = 20;
    if (depth > 0 && depth < 100) {
      depthValue = depth;
    }
    path += `?depth=${depthValue}`;

    const response = await this.httpClient.get(path, this.getHeaders());
    const orderbook = response.body as RawOrderbook;

    if (convertToDecimal && orderbook) {
      if (orderbook.bids && Array.isArray(orderbook.bids)) {
        orderbook.bids = (orderbook.bids as [string, string][]).map(
          ([price, size]) => [
            fromSatoshi(price),
            fromSatoshi(size),
          ],
        );
      }
      if (orderbook.asks && Array.isArray(orderbook.asks)) {
        orderbook.asks = (orderbook.asks as [string, string][]).map(
          ([price, size]) => [
            fromSatoshi(price),
            fromSatoshi(size),
          ],
        );
      }
    }

    return orderbook;
  }

  /**
   * Get market trades by market ID
   * Uses HTTP endpoint /clob/v1/trades?market_id={marketId} (matches Go client GetMarketTrades)
   * Response format: { trades: RawTrade[], total_count: number, market_id: string, symbol: string, timestamp: string }
   */
  async getMarketTrades(marketId: string, limit?: number): Promise<RawTrade[]> {
    let path = `/clob/v1/trades?market_id=${marketId}`;
    if (limit && limit > 0) {
      path += `&limit=${limit}`;
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    console.debug(
      "[InfoClient] getMarketTrades response for marketId:",
      marketId,
      "response:",
      response.body,
    );

    const body = response.body as TradesResponse;

    // Handle the actual API response structure
    if (
      body &&
      typeof body === "object" &&
      "trades" in body &&
      Array.isArray(body.trades)
    ) {
      return body.trades;
    }

    // Fallback for legacy formats (direct array or { data: [...] })
    if (Array.isArray(body)) {
      return body;
    }
    // Check for legacy format with 'data' property (not part of TradesResponse type)
    const bodyAny = body as any;
    if (bodyAny?.data && Array.isArray(bodyAny.data)) {
      return bodyAny.data;
    }

    console.warn(
      "[InfoClient] getMarketTrades: Unexpected response structure:",
      response.body,
    );
    // Return empty array if response structure is unexpected
    return [];
  }

