  /**
   * Get positions for authenticated user
   * Uses HTTP endpoint /bucket/v1/address/{address}/positions (matches Go client GetUserPositions)
   * Note: Requires walletAddress to be set or passed
   * 
   * Actual API response: Direct array of RawPosition objects
   * Also handles wrapped format { positions: [...] } for backward compatibility
   */
  async getPositions(
    walletAddress: string,
    activeOnly?: boolean,
  ): Promise<RawPosition[]> {
    if (!walletAddress) {
      throw new Error("walletAddress is required for getPositions");
    }
    let path = `/bucket/v1/address/${walletAddress}/positions`;
    if (activeOnly !== undefined) {
      path += `?active_only=${activeOnly}`;
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    const body = response.body as PositionsResponse | RawPosition[];
    // Normalize response to always return an array
    // Handle cases where API returns direct array (actual format) or wrapped { positions: [...] }
    if (Array.isArray(body)) {
      return body;
    }
    if (
      body &&
      typeof body === "object" &&
      "positions" in body &&
      Array.isArray(body.positions)
    ) {
      return body.positions;
    }
    // Return empty array if response structure is unexpected
    return [];
  }

  /**
   * Get balance for an address.
   * Uses HTTP endpoint /bank/v1/balance/{address}?collateralAssetIndex= per bank_api.
   * @param address - Account address
   * @param collateralAssetIndex - Asset ticker (e.g. MORM, USDC). Replaces legacy 'asset' param.
   */
  async getBalance(
    address: string,
    collateralAssetIndex: string,
  ): Promise<RawBalance> {
    const path = `/bank/v1/balance/${encodeURIComponent(address)}?collateralAssetIndex=${encodeURIComponent(collateralAssetIndex)}`;
    const response = await this.httpClient.get(path, this.getHeaders());
    const b = response.body as RawBalance;
    // Normalize: if API returns only collateralAssetIndex, set asset for backward compat
    if (!b.asset && (b as any).collateralAssetIndex) {
      const t = (b as any).collateralAssetIndex?.ticker ?? (b as any).collateralAssetIndex;
      if (typeof t === "string") return { ...b, asset: t };
    }
    return b;
  }

  /**
   * Get balances for an address
   * Uses HTTP endpoint /bank/v1/balances/{address} (matches Go client GetBalances)
   *
   * Actual API response structure:
   * {
   *   "address": "0x...",
   *   "balances": [
   *     {
   *       "asset": "MORM",
   *       "balance": "100000000000000000000000",
   *       "available_balance": "100000000000000000000000",
   *       "locked_balance": "0"
   *     },
   *     ...
   *   ],
   *   "shard_id": ""
   * }
   */
  async getBalances(address: string): Promise<RawBalance[]> {
    const response = await this.httpClient.get(
      `/bank/v1/balances/${address}`,
      this.getHeaders(),
    );
    const body = response.body as GetBalancesResponse;

    // Handle the actual API response structure (GetBalancesResponse)
    if (
      body &&
      typeof body === "object" &&
      "balances" in body &&
      Array.isArray(body.balances)
    ) {
      // Normalize: when API returns collateralAssetIndex.ticker only, set asset for backward compat
      return body.balances.map((b: RawBalance) => {
        if (b.asset) return b;
        const ticker = (b as any).collateralAssetIndex?.ticker ?? (b as any).collateralAssetIndex;
        if (ticker && typeof ticker === "string") {
          return { ...b, asset: ticker };
        }
        return b;
      });
    }

    return [];
  }

  /**
   * Get balance with decimal conversion. Passes assetID as collateralAssetIndex to getBalance.
   * @param assetID - Collateral asset ticker (e.g. MORM, USDC).
   */
  async getBalanceDecimal(
    address: string,
    assetID: string,
  ): Promise<RawBalance> {
    const balance = await this.getBalance(address, assetID);

    if (balance && typeof balance === "object") {
      const converted: RawBalance & Record<string, any> = { ...balance };
      if (converted.balance) {
        converted.balance = fromSatoshi(converted.balance) as any;
      }
      // Convert available_balance (snake_case from API) to available (camelCase). Do NOT overwrite
      // available_balance so callers using toNumberFromSatoshi(balance.available_balance) or
      // fromSatoshi(balance.available_balance) still receive the raw satoshi string.
      if (converted.available_balance) {
        converted.available = fromSatoshi(converted.available_balance);
      }
      if (converted.available && typeof converted.available === "string") {
        converted.available = fromSatoshi(converted.available);
      }
      if (converted.locked_balance) {
        converted.locked = fromSatoshi(converted.locked_balance);
        // Do NOT overwrite locked_balance; keep raw satoshi for backward compatibility.
      }
      if (converted.locked && typeof converted.locked === "string") {
        converted.locked = fromSatoshi(converted.locked);
      }
      // Type assertion: converted has all RawBalance properties (spread from balance)
      // even though some values are converted from string to number
      return converted as RawBalance;
    }

    return balance;
  }

